<template>
  <div class="main">
    <div class="wrapper">
      <el-button style="float:right" @click="handleCreate">
        <i class="el-icon-plus"></i>添加
      </el-button>
      <el-table :data="roleList">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="roleName" label="名称"></el-table-column>
        <el-table-column prop="comment" label="comment"></el-table-column>
        <el-table-column label="action">
          <template slot-scope="scope">
            <el-button type="primary" @click="handleEdit(scope)">编辑</el-button>
            <el-button type="danger" @click="deleteRole(scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog :title="titleMap[dialogStatus]" :visible.sync="dialogVisible" width="30%">
        <el-form :model="role" label-width="80px" :rules="checkRules()" ref="role">
          <el-form-item label="name" prop="roleName">
            <el-input v-model="role.roleName"></el-input>
          </el-form-item>
          <el-form-item label="commet" prop="comment">
            <el-input v-model="role.comment"></el-input>
          </el-form-item>
          <el-form-item label="menu">
            <el-tree
              ref="tree"
              :check-strictly="checkStrictly"
              :data="routes"
              :props="defaultProps"
              node-key="path"
              show-checkbox
            ></el-tree>
          </el-form-item>
        </el-form>

        <span slot="footer">
          <el-button @click=" dialogVisible= false">取 消</el-button>
          <el-button
            type="primary"
            @click="dialogStatus=='create'?handleAddRole(): handleUpdate()"
          >确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import path from "path";
import _ from "lodash";
import { getAllRoleList, getAllRoutes } from "@/api/roleList";
const defaultRole = {
  id: undefined,
  roleName: "",
  comment: "",
  routes: []
};
export default {
  data() {
    return {
      routesData: [],

      roleList: [],
      defaultProps: {
        children: "children",
        label: "name"
      },
      titleMap: {
        edit: "edit",
        create: "create"
      },
      dialogStatus: "",
      checkStrictly: false,
      dialogVisible: false,
      role: {
        id: "",
        roleName: "",
        comment: "",
        routes: []
      },
      rules_edit: {
        roleName: [{ required: true, message: "请输入姓名", trigger: "change" }]
      },
      rules_create: {
        roleName: [{ required: true, message: "请输入姓名", trigger: "blur" }]
      }
    };
  },
  created() {
    this.getData();
  },
  computed: {
    routes() {
      return this.generateRoutes(this.routesData);
    }
  },
  methods: {
    getData() {
      getAllRoutes().then(resp => {
        this.routesData = resp.data;
      });

      getAllRoleList().then(resp => {
        this.roleList = resp.data;
      });
    },

    checkRules() {
      if (this.dialogStatus === "edit") {
        return this.rules_edit;
      } else {
        return this.rules_create;
      }
    },

    generateRoutes(routes, basePath = "/") {
      let res = [];
      for (let route of routes) {
        if (route.hidden) {
          continue;
        }
        if (
          !route.children ||
          (route.children.length == 1 && route.children.hidden)
        ) {
          let data = {
            name: route.meta && route.meta.title,
            path: path.resolve(basePath, route.path),
            children: null
          };
          res.push(data);
        } else {
          let data = {
            name: route.meta && route.meta.title,
            path: path.resolve(basePath, route.path),
            children: null
          };
          if (route.children) {
            data.children = this.generateRoutes(route.children, data.path);
          }
          res.push(data);
        }
      }
      return res;
    },

    generateArr(routes) {
      let res = [];
      routes.forEach(route => {
        if (route.children && route.children.length > 0) {
          const tmp = this.generateArr(route.children);
          res = [...res, ...tmp];
        } else {
          res.push(route);
        }
      });
      return res;
    },

    generateTree(routes, basePath = "/", checkedKeys) {
      const res = [];

      for (const route of routes) {
        const routePath = path.resolve(basePath, route.path);
        // recursive child routes
        if (route.children) {
          route.children = this.generateTree(
            route.children,
            routePath,
            checkedKeys
          );
        }

        if (
          checkedKeys.includes(routePath) ||
          (route.children && route.children.length >= 1)
        ) {
          res.push(route);
        }
      }
      return res;
    },

    handleCreate() {
      this.role = Object.assign({}, defaultRole);
      if (this.$refs.tree) {
        this.$refs.tree.setCheckedNodes([]);
      }
      this.dialogStatus = "create";
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.$refs["role"].clearValidate();
      });
    },

    handleEdit(scope) {
      this.dialogVisible = true;
      this.dialogStatus = "edit";
      this.role = _.cloneDeep(scope.row);
      this.$nextTick(() => {
        const routes = this.generateRoutes(this.role.routes);
        this.$refs.tree.setCheckedNodes(this.generateArr(routes));
        this.checkStrictly = false;
        this.$refs["role"].clearValidate();
      });
    },

    handleUpdate() {
      this.$refs["role"].validate(valid => {
        if (valid) {
          const checkedKeys = this.$refs.tree.getCheckedKeys();
          this.role.routes = this.generateTree(
            _.cloneDeep(this.routesData),
            "/",
            checkedKeys
          );

          for (let index = 0; index < this.roleList.length; index++) {
            if (this.roleList[index].id === this.role.id) {
              this.roleList.splice(index, 1, Object.assign({}, this.role));
              break;
            }
          }
          this.dialogVisible = false;
        } else {
          return false;
        }
      });
    },

    handleAddRole() {
      this.$refs["role"].validate(valid => {
        if (valid) {
          const checkedKeys = this.$refs.tree.getCheckedKeys();
          this.role.routes = this.generateTree(
            _.cloneDeep(this.routesData),
            "/",
            checkedKeys
          );
          this.roleList.push(this.role);
          this.dialogVisible = false;
        }else{
          return false;
        }
      });
    },

    deleteRole(scope) {
      this.$confirm("Confirm to remove the role?", "Warning", {
        ConfirmButtonText: "Confirm",
        CancelButtonText: "Cancel"
      }).then(() => {
        for (let index = 0; index < this.roleList.length; index++) {
          if (this.roleList[index].role_name == scope.row.role_name) {
            this.roleList.splice(index, 1);
            break;
          }
        }
        this.$message({
          type: "success",
          message: "success"
        });
      }).catch(()=>{
        return;
      });
    },

    updateRoutes(routes, roleRoutes, roleName) {
      routes.forEach(route => {
        route.matched = false;
        for (let i of roleRoutes) {
          if (
            route.path === i.path ||
            route.path == "*" ||
            route.path == "dashboard"
          ) {
            route.matched = true;
            if (!route.meta.roles.some(role => role === roleName)) {
              route.meta.roles.push(roleName);
            }
            if (route.children && i.children) {
              this.updateRoutes(route.children, i.children, roleName);
            }
            break;
          }
        }
        if (!route.matched) {
          this.clearRole(route, roleName);
        }
      });
    },

    clearRole(routes, roleName) {
      //console.log(routes);
      if (Array.isArray(routes)) {
        for (let route of routes) {
          for (let index = 0; index < route.meta.roles.length; index++) {
            if (route.meta.roles[index] === roleName) {
              route.meta.roles.splice(index, 1);
            }
          }
          route.matched = false;
          if (route.children) {
            this.clearRole(route.children, roleName);
          }
        }
      } else {
        for (let index = 0; index < routes.meta.roles.length; index++) {
          if (routes.meta.roles[index] === roleName) {
            routes.meta.roles.splice(index, 1);
          }
        }
        if (routes.children) {
          this.clearRole(routes.children, roleName);
        }
      }
    }
  }
};
</script>